import React from 'react';
import Layout from './../shared/Layout'

function GoldenAge() {
  return (
    <Layout>
      <div className = 'container'>
        <h1>
          The Golden Age<small className = 'text-muted'>Saturday, September 15, 2012</small>
        </h1>

        <p>Наступавшая золотая осень, радовала глаза, чудесным образом наполняя новыми красками каждый живой листок. Яркое солнце всеми силами согревало землю, преподнося летнее настроение к трону вошедшей в права правления осени. Времени года, навсегда вошедшего в историю города, белоснежно белые стены, крепости которого, гордо возвышались на противоположном берегу Куйбышевского водохранилища. Здесь, посреди холмов, где по приданию без всякого огня кипит вода, стоит тысячелетний город, основанный булгарами, как пограничная крепость на севере их государства. Такой Казань предстала и пред Улу-Мухамеддом, золотоордынским ханом, и пред войском Ивана IV. Возвышаясь над временем, город невозмутимо поднимался с колен, очаровывая добротой и гостеприимством объединяя в своих границах воевавших по разные стороны народы. Таким она предстала и передо мной, вернувшимся в Казань спустя долгие шесть лет.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_016_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Этому предшествовало, начавшееся еще до рассвета солнца, длительное путешествие сквозь меняющиеся пейзажи трех регионов России. Где редкие леса, разбитые многочисленными деревеньками Нижегородской области постепенно переходили в бескрайний хвойный лес, раскинувшийся по территории Чувашии, рассеченной чередой оврагов и речушек. Дорога меняла вид, с каждым километром неся на восток, как географический, так и культурный. Расставшись с последним поворотом перед границей Татарстана, шоссейное полотно, окруженное нетронутыми человеком полями, устремилось к небосводу, у границы которого, спустя час ожидания, стоя между высокими холмами, на противоположном берегу Волги, предстала Казань.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_001_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_002_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Прибыв в Казань с друзьями, ранее не имевшими возможности окунуться в атмосферу местной культуры, я был, несомненно, рад слышать их отзывы, основанные по большей части на сильной градации, так пестро проводившей черту меж нашими народами и культурами. Здесь не было места грубости Нижнего, и царящего в нем безразличия к окружающим, на место эгоистичному отношению приходило местное гостеприимство, роднившее с Питером.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_003_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_004_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_005_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Посреди Кремля, расположившись у западных стен, стоит главная соборная мечеть республики - Кул Шариф, названная в честь её последнего имама сеида Кул-Шарифа, одного из предводителей обороны Казани. Будучи разрушенной войсками Ивана Грозного, она была построена вновь, обретя свой современный вид к празднованию тысячелетия города.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_006_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_007_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_008_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_009_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_010_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />

        <p>Падающая башня Сююмбике, служащая предметом многолетних споров историков, так и не выяснившим дату основания, она стоит, отклонившись от своей вертикали на целых два метра. Вокруг башни ходит много легенд, сходящихся к одной из первых мусульманок, взявших на себя бремя правления государством, - Сююмбике, царице Казанского ханства.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_013_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_012_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Одна из красивейших достопримечательностей города - улица Баумана, где издавна расположен центр торговли и развлечений. Ее название многократно менялось, чему способствовало, и взятие Казани, и появлению Новой слабоды в ее южной части, и происхождения революционера Николая Баумана. На протяжении улицы стоит много памятников и исторических зданий - многострадальная гостиница "Казань", с балкона которой выступал Троцкий, памятник певцу Шаляпину, фонтаны-памятники героям татарских сказок и многие другие, лежащих в основе прекрасного декора города.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_044_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_014_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_046_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_015_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_043_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_038_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Пирамида - здание развлекательного центра, памятник символизирующий торжество современной архитектуры.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_017_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_020_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_018_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_024_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_023_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_022_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_021_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_027_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Памятник Мусе Джалилю, установленного в память о герое Сюза, организовавшего антифашистское подполье, поставившего целью осуществление восстания военнопленных.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_029_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_030_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_031_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_032_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_034_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_033_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_035_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_037_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Часы универсиады, поставленные напротив знаменитых часов на Баумана, уникальный арт-объект отсчитывающий время до начала Казанской универсиады.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_039_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image vertical' />
        <img src = '../albums/2012-09-14_15/2012-09-14_15_040_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />

        <p>Короткие два дня путешествия пролетели мимолетом, оставив позади воспоминания и неизменное желание вернуться вновь. Вслед за Казанью ушла и теплая погода, оставив один на один, с морозной осенью, пестрящей красно-желтыми красками, словно сходящими с картины Левитана.</p>

        <img src = '../albums/2012-09-14_15/2012-09-14_15_041_large.jpg' alt = 'Andrey Pudov - The Golden Age' className = 'img-responsive gallery-image' />
      </div>
    </Layout>
  );
}

export default GoldenAge;